.style-editor-panel {
  width: 100%;
  padding: 16px;
  background: #f5f5f5;
  border-radius: 8px;
  max-height: 600px;
  overflow-y: auto;

  mat-accordion {
    margin-bottom: 12px;
  }

  mat-expansion-panel-header {
    mat-panel-title {
      display: flex;
      align-items: center;
      gap: 8px;

      mat-icon {
        font-size: 20px;
        width: 20px;
        height: 20px;
      }
    }
  }

  .style-section {
    padding: 16px 0;
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  .full-width {
    width: 100%;
  }

  .color-picker-group {
    display: flex;
    flex-direction: column;
    gap: 8px;

    label {
      font-size: 14px;
      font-weight: 500;
      color: #333;
    }

    .color-input-group {
      display: flex;
      gap: 8px;
      align-items: center;

      .color-input {
        width: 50px;
        height: 40px;
        border: 1px solid #ccc;
        border-radius: 4px;
        cursor: pointer;
        padding: 0;

        &::-webkit-color-swatch-wrapper {
          padding: 2px;
        }

        &::-webkit-color-swatch {
          border: none;
          border-radius: 2px;
        }
      }

      .color-text-input {
        flex: 1;
        height: 40px;
        padding: 0 12px;
        border: 1px solid #ccc;
        border-radius: 4px;
        font-family: monospace;
        font-size: 14px;

        &:focus {
          outline: none;
          border-color: #2196F3;
        }
      }
    }

    .preset-colors {
      display: grid;
      grid-template-columns: repeat(10, 1fr);
      gap: 4px;

      .preset-color-btn {
        width: 100%;
        aspect-ratio: 1;
        border: 2px solid #ccc;
        border-radius: 4px;
        cursor: pointer;
        transition: all 0.2s;
        padding: 0;

        &:hover {
          transform: scale(1.1);
          box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
        }

        &.active {
          border-color: #2196F3;
          border-width: 3px;
        }
      }
    }
  }

  .button-group {
    display: flex;
    gap: 4px;
    background: white;
    border-radius: 4px;
    padding: 4px;
    width: fit-content;

    button {
      transition: all 0.2s;

      &.active {
        background: #2196F3;
        color: white;

        mat-icon {
          color: white;
        }
      }

      &:not(.active):hover {
        background: #e3f2fd;
      }
    }
  }
}

// 스크롤바 스타일
.style-editor-panel::-webkit-scrollbar {
  width: 8px;
}

.style-editor-panel::-webkit-scrollbar-track {
  background: #e0e0e0;
  border-radius: 4px;
}

.style-editor-panel::-webkit-scrollbar-thumb {
  background: #bdbdbd;
  border-radius: 4px;

  &:hover {
    background: #9e9e9e;
  }
}
